import { Box, Flex, Image } from 'rebass';
import '../oweek.css';

const Coundown = () => {

    var today = new Date();
    console.log(today);
    var day = today.getDate();
    var month = today.getMonth() + 1;
    var year = today.getFullYear();

    let daysToGo = 0;

    if (year === 2019) {
        if (month === 6) {
            daysToGo = 18 + 31 + (30 - day);
        }
        if (month === 7) {
            daysToGo = 18 + (31 - day);
        }
        if (month === 8) {
            daysToGo = (18 - day);
        }
    }

    return (
        <div className='oweek-countdown'>
            <p>{daysToGo} days until O-Week!</p>
        </div>
    )
}

const Homepage = () => (
    <div className='oweek-homepage'>
        <Flex flexDirection='row' justifyContent='center' flexWrap='wrap'>
            <Box width={[1, 0.5]}>
                <Image src='../../../static/oweek/mcscottishyurt.jpg' alt='' />
            </Box>
            <Box width={[1, 0.5]}>
                <Image src='../../../static/oweek/cardmovin.jpg' alt='' />
            </Box>
            <Box width={[1, 0.5]} style={{margin: '0'}}>
                <Image src='../../../static/oweek/jumpingCoords.jpg' alt='' />
            </Box>
            <Box width={[1, 0.5]}>
                <Image src='../../../static/oweek/colorceleb.jpg' alt='' />
            </Box>
        </Flex>
        <Coundown />
    </div>
)

export default Homepage;